import { Component, OnInit } from '@angular/core';
import {ProjectService} from '../services/project.service';

@Component({
  selector: 'app-location',
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.css']
})
export class LocationComponent implements OnInit {

  neighborhoodList : any[];
  mapDataString :  string;

  constructor(private service: ProjectService) { }

  ngOnInit() {

    this.service.getSampleData().subscribe(response  => {
      this.mapDataString =  response.json();
        const jsonMapString = JSON.stringify(this.mapDataString);
        JSON.parse(jsonMapString, (key, value) => {
            if (key === 'result') {

                this.neighborhoodList = value;

            }
        });

  });

}

}



