import { Component,OnInit} from '@angular/core';
import {ProjectService} from './services/project.service';




@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit {

  imageresultArray: any[];
sampleDataArray: any[];
photoUrlArray: string[] = [];
imageDataString: string;
sampledataString: string;



constructor(private service: ProjectService) {}

ngOnInit() {
  this.service.getSampleData().subscribe(response => {
      this.sampledataString = response.json();
      const jsonsampleData = JSON.stringify(this.sampledataString);
      JSON.parse(jsonsampleData, (key, value) => {
          if (key === 'result') {
              this.sampleDataArray = value;
          }

      });
      this.sampleDataArray.forEach(dataElement => {

      });
  }, error => {

  });




  this.service.getImageData().subscribe(response => {
      this.imageDataString = response.json();
      const jsonStringfy = JSON.stringify(this.imageDataString);
      JSON.parse(jsonStringfy, (key, value) => {
          if (key === 'result') {
              this.imageresultArray = value;
          }

      });

      this.imageresultArray.forEach(imagedataElement => {
          imagedataElement.amenities.forEach(amentieItem => {
              this.photoUrlArray.push(amentieItem.photo.url);

          });
      });

  }, error => {

  });




}


}



