import { Injectable } from '@angular/core';
 import { Http } from '@angular/http';

import { Observable } from 'rxjs/Observable';

 import 'rxjs/add/operator/catch';



@Injectable()
export class ProjectService {




private sampleDataUrl: string = 'https://prodapi.livezelo.com/pgs/details/57d67349631877034ff85ab2.json';

private imageDataUrl :  string = 'https://prodapi.livezelo.com/v2/pgs/57d93a2f631877034ff86c27/photos.json'

private mapUrl :  string = 'http://prodapi.livezelo.com/centers/search-nearby-pgs-optimized?latitude=12.926804&longitude=77.670238';

constructor(private http: Http) { }

getSampleData(){
return  this.http.get(this.sampleDataUrl);


}

getImageData(){
return  this.http.get(this.imageDataUrl);


}

getMapData(){
return  this.http.get(this.mapUrl);


}




}



