import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';


import { AppComponent } from './app.component';

import {ProjectService} from './services/project.service';
import { FormsModule } from '@angular/forms';


import { RouterModule, Routes } from '@angular/router';
import { FoodComponent } from './food/food.component';
import { RoomtypeComponent } from './roomtype/roomtype.component';
import { MainEnteranceComponent } from './main-enterance/main-enterance.component';
import { SingleSharingComponent } from './roomtype/single-sharing/single-sharing.component';
import { DobuleSharingComponent } from './roomtype/dobule-sharing/dobule-sharing.component';
import { TripleSharingComponent } from './roomtype/triple-sharing/triple-sharing.component';
import { AmentiesComponent } from './amenties/amenties.component';
import { DescriptionComponent } from './description/description.component';
import { LocationComponent } from './location/location.component';





const appRoutes : Routes = [
 {path : '', component : DescriptionComponent},
{path : 'roomType', component : RoomtypeComponent  },
{path : 'roomType/singleSharing', component : SingleSharingComponent  },
{path : 'roomType/doubleSharing', component : DobuleSharingComponent  },
{path : 'roomType/tripleSharing', component : TripleSharingComponent  },
{path : 'food', component : FoodComponent},
{path : 'amenties', component : AmentiesComponent},
{path : 'location', component : LocationComponent}

];

@NgModule({
  declarations: [
    AppComponent,
    FoodComponent,
    RoomtypeComponent,
    MainEnteranceComponent,
    SingleSharingComponent,
    DobuleSharingComponent,
    TripleSharingComponent,
    AmentiesComponent,
    DescriptionComponent,
    LocationComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    RouterModule.forRoot(appRoutes)

  ],
  providers: [ProjectService],
  bootstrap: [AppComponent]
})
export class AppModule { }



