import {
    Component,
    OnInit
} from '@angular/core';
import {
    ProjectService
} from '../services/project.service';
import {
    AppComponent
} from '../app.component';


@Component({
    selector: 'app-description',
    templateUrl: './description.component.html',
    styleUrls: ['./description.component.css']
})


export class DescriptionComponent implements OnInit {



    sampleresultArray: any[];
    sampleDataString: string;


    constructor(private service: ProjectService) {}



    ngOnInit() {
        this.service.getSampleData().subscribe(response => {
            this.sampleDataString = response.json();

            const jsonStringfy = JSON.stringify(this.sampleDataString);


            JSON.parse(jsonStringfy, (key, value) => {
                if (key === 'result') {

                    this.sampleresultArray = value;

                }
                return value;
            });




        });


    }




}



